/*////////////////////////////////////
// CSS of Header
/////////////////////////////////////*/

.header {
  width: 100%;
  height: 6.4rem;
  background-color: var(--color-primary-black);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 888;
}

.header-container {
  height: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@media screen and (max-width: 767px) {
  .header-container {
    -ms-flex-pack: center;
        justify-content: center;
  }
}


